export default {
    
    getModalActionAndData: state =>  state,

    getModalTitle: state => {
        switch (state.heading) {
            case 'report_title':
                return 'સમસ્યાનો વિષય';
            case 'edit_pratilipi_summary':
            	return 'રચનાનો સારાંશ';
            case 'edit_pratilipi_title':
            	return 'શીર્ષક';
            case 'pratilipi_delete_content':
                return 'રદ કરો';
        }
    },

    getInputModalSubheadings: state => {
    	switch (state.heading) {
            case 'report_title':
                return ['સમસ્યાનો વિષય'];
            case 'edit_pratilipi_summary':
            	return ['ટૂંકમાં સારાંશ લખો'];
            case 'edit_pratilipi_title':
            	return ['અહી શીર્ષક લખો', 'શીર્ષક અંગ્રેજીમાં લખો'];
            default:
            	return []
        }	
    },

    getModalMessage: state => {
        switch (state.message) {
            case 'pratilipi_confirm_delete_content':
                return 'રચના રદ કરવા માંગો છો?';
        }
    }
}



